/* ================== SPECIAL CASES ================== */

/*  shared across all device types */
div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"],
div#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget[survey-widget-type="bottombarsurvey"] {
	border-top: 3px solid #646A70 !important;
	box-shadow: 0px -1px 10px rgba(0,0,0,0);
}

div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question,
div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question {
	margin-top: 10px !important;
	margin-bottom: 15px !important;
}

#_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="free_text_question"] ul._pi_answers_container {
	flex-wrap: wrap !important;
}

	/*  desktop */
	@media (min-width:769px) {
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question, 
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_thankYouSurvey,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question p:first-child,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_invitationTextContainer	{
			font-size: 24px !important;
			letter-spacing: .025rem;
		}
		
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_free_text_question_field_container {
			flex: 	0 0 750px !important;
			margin: 	0 auto 10px !important;
		}
	}

	/*  tablet */
	@media (min-width:415px) and (max-width:768px) {
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question, 
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_thankYouSurvey,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question p:first-child,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_invitationTextContainer	{
			font-size: 22px !important;
			letter-spacing: .025rem;
		}
		
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_free_text_question_field_container {
			flex: 	0 0 600px !important;
			margin: 	0 auto 10px !important;
		}
	}

	/*  mobile */
	@media (max-width:414px) {
		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question, 
		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_thankYouSurvey,
		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question,
		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_question_custom_content_question p:first-child,
		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_invitationTextContainer	{
			font-size: 20px !important;
			letter-spacing: .025rem;
		}

		div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_free_text_question_field_container {
			flex: 	0 0 100% !important;
			margin: 	0 auto 10px !important;
		}
	}



	/*  SUBMIT BUTTON CONTAINER | desktop and mobile */
	div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] 
		._pi_free_text_question_submit_button_container,	
	div#_pi_surveyWidgetContainer.mobile-enabled 
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] 
		._pi_widgetContentContainer ul._pi_answers_container 
		._pi_free_text_question_submit_button_container {
		padding-left: 		0px !important;
		flex: 				1 1 100% !important;
		margin-bottom: 	10px !important;
/* 		background-color: yellow !important; */
	}


	


	/* CONTENT CONTAINER FOR SPECIAL CONTENT WIDTH, TO ALIGN CLOSE BUTTON */
	@media (min-width:1066px) {
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] div._pi_widgetContentContainer,
		div#_pi_surveyWidgetCustom[survey-widget-type="bottombarsurvey"] div._pi_widgetContentContainer {
			max-width: 	1065px !important;
/* 			background-color: yellow !important; */
		}

		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_closeButton {
			font-size: 34px;
			font-weight: 100;
			height: 30px !important;
			width: 20px !important;
			margin-left: 1045px !important;
/* 			background-color: green !important; */
		}	
	}

	/* BIG CLOSE BUTTON */
	@media (max-width:1065px) {
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_closeButton,
		div#_pi_surveyWidget[survey-widget-type="bottombarsurvey"] ._pi_closeButton:hover {
			font-size: 34px;
			font-weight: 100;
			height: 30px !important;
			width: 20px !important;
			margin-left: calc(100% - 60px) !important;
			margin-top: 10px !important;
/* 			background-color: green !important; */
		}
	}